    commentary: `अर्जुन सोचते हैं कि भगवान् ऐसा समझते होंगे कि अर्जुन युद्ध करेगा तो उसकी विजय होगी, और विजय होने पर उसको राज्य मिल जायगा, जिससे उसके चिन्ता-शोक मिट जायँगे और संतोष हो जायगा। परन्तु शोक के कारण मेरी ऐसी दशा हो गयी है कि विजय होने पर भी मेरा शोक दूर हो जाय, ऐसी बात मैं नहीं देखता।
	अगर मुझे धनधान्य से सम्पन्न और निष्कण्टक राज्य मिल जाय अर्थात् जिस राज्य में प्रजा खूब सुखी हो, प्रजा के पास खूब धन-धान्य हो, किसी चीज की कमी न हो और राज्य में कोई वैरी भी न हो, ऐसा राज्य मिल जाय, तो भी मेरा शोक दूर नहीं हो सकता।`,
