    translation:
      "।।2.20।। यह शरीरी न कभी जन्मता है और न मरता है तथा यह उत्पन्न होकर फिर होने वाला नहीं है। यह जन्मरहित, नित्य-निरन्तर रहने वाला, शाश्वत और पुराण (अनादि) है। शरीर के मारे जानेपर भी यह नहीं मारा जाता।",
    commentary:
      " 2.20।। व्याख्या, [शरीर में छः विकार होते हैं, उत्पन्न होना, सत्तावाला दीखना, बदलना, बढ़ना, घटना और नष्ट होना   । यह शरीरी इन छहों विकारोंसे रहित है, यही बात भगवान् इस श्लोक में बता रहे हैं]   ।\n\t'न जायते म्रियते वा कदाचिन्न', जैसे शरीर उत्पन्न होता है, ऐसे यह शरीरी कभी भी, किसी भी समयमें उत्पन्न नहीं होता। यह तो सदा से ही है। भगवान ने इस शरीरी को अपना अंश बताते हुए इसको 'सनातन' कहा है 'ममैवांशो जीवलोके जीवभूतः सनातनः' (15। 7)।",
