    commentary:
      " 2.37।। व्याख्या, 'हतो वा प्राप्स्यसि स्वर्गं जित्वा वा भोक्ष्यसे महीम्', इसी अध्याय के छठे श्लोक में अर्जुन ने कहा था कि हम लोगों को इसका भी पता नहीं है कि युद्ध में हम उनको जीतेंगे यह वे हमको जीतेंगे। अर्जुन के इस सन्देहको लेकर भगवान् यहाँ स्पष्ट कहते हैं कि अगर युद्ध में तुम कर्ण आदि के द्वारा मारे भी जाओगे तो स्वर्ग को चले जाओगे और अगर युद्ध में तुम्हारी जीत हो जायगी तो यहां पृथ्वी का राज्य भोगोगे। इस तरह तुम्हारे तो दोनों ही हाथों में लड्डू हैं। तात्पर्य है कि युद्ध करने से तो तुम्हारा दोनों तरफ से लाभ-ही-लाभ है और युद्ध न करने से दोनों तरफ से हानि-ही-हानि है। अतः तुम्हें युद्ध में प्रवृत्त हो जाना चाहिये।",
