    commentary:
      "।।2.50।। व्याख्या, 'बुद्धियुक्तो जहातीह उभे सुकृतदुष्कृते', समतायुक्त मनुष्य जीवित अवस्था में ही पुण्य-पाप का त्याग कर देता है अर्थात् उसको पुण्य-पाप नहीं लगते, वह उनसे रहित हो जाता है। जैसे संसार में पुण्य-पाप होते ही रहते हैं, पर सर्वव्यापी परमात्माको वे पुण्य-पाप नहीं लगते, ऐसे ही जो समतामें निरन्तर स्थित रहता है, उसको पुण्यपाप नहीं लगते (गीता 2। 38)।\nसमता एक ऐसी विद्या है जिससे मनुष्य संसार में रहता हुआ ही संसार से सर्वथा निर्लिप्त रह सकता है। जैसे कमलका पत्ता जलसे ही उत्पन्न होता है, और जलमें ही रहता है, पर वह जलसे लिप्त नहीं होता, ऐसे ही समतायुक्त पुरुष संसार में रहते हुए भी संसार से निर्लिप्त रहता है। पुण्य-पाप उसका स्पर्श नहीं करते अर्थात् वह पुण्य-पाप से असङ्ग हो जाता है।\nवास्तव में यह स्वयं (चेतन-स्वरूप) पुण्य-पाप से रहित ही। केवल असत् पदार्थों, शरीरादिके साथ सम्बन्ध जोड़नेसे ही पुण्य-पाप लगते हैं। अगर यह असत् पदार्थोंके साथ सम्बन्ध न जोड़े, तो यह आकाशकी तरह निर्लिप्त रहेगा, इसको पुण्य-पापनहीं लगेंगे।\n\t 'तस्माद्योगाय युज्यस्व', इसलिये तुम योगमें लग जाओ अर्थात् निरन्तर समतामें स्थित रहो। वास्तव में समता तुम्हारा स्वरूप है। अतः तुम नित्य-निरन्तर समतामें ही स्थित रहते हो। केवल राग-द्वेष के कारण तुम्हारेको उस समताका अनुभव नहीं हो रहा है। अगर तुम हरदम समतामें स्थित न रहते, तो सुख और दुःख का ज्ञान तुम्हें कैसे होता; क्योंकि ये दोनों ही अलग-अलग हैं। जब इन दोनों का तुम्हें ज्ञान होता है तो तुम इन के आने-जानेमें सदा समरूपसे रहते हो। इसी समताका तुम अनुभव करो।\n\t 'योगः कर्मसु कौशलम्', कर्मोंमें योग ही कुशलता है अर्थात् कर्मोंकी सिद्धि-असिद्धिमें और उन कर्मोंके फलके प्राप्ति-अप्राप्ति में सम रहना ही कर्मोंमें कुशलता है। उत्पत्ति-विनाशशील कर्मोंमें योगके सिवाय दूसरी कोई महत्त्वकी चीज नहीं है।\nइन पदोंमें भगवान ने योगकी परिभाषा नहीं बतायी है, प्रत्युत योगकी महिमा बतायी है। अगर इन पदोंका अर्थ\n'कर्मोंमें कुशलता ही योग है', ऐसा किया जाय तो क्या आपत्ति है? अगर ऐसा अर्थ किया जायगा तो जो बड़ी कुशलतासे, सावधानीपूर्वक चोरी करता है, उसका वह चोरीरूप कर्म भी योग हो जायगा। अतः ऐसा अर्थ करना अनुचित है। कोई कह सकता है कि हम तो विहित कर्मोंको ही कुशलतापूर्वक मारने का नाम योग मानते हैं। परन्तु ऐसा मानने से मनुष्य कुशलतापूर्वक साङ्गोपाङ्ग किये गये कर्मोंके फलमें बँध जायगा, जिससे उसकी स्थिति समतामें नहीं रहेगी। अतः यहाँ 'कर्मोंमें योग ही कुशलता है', ऐसा अर्थ लेना ही उचित है। कारण कि कर्मोंको करते हुए भी जिसके अन्तःकरण में समता रहती है, वह कर्म और उनके फलमें बँधेगा नहीं। इसलिये उत्पत्ति-विनाशशील कर्मोंको करते हुए सम रहना ही कुशलता है, बुद्धिमानी है।\nदूसरी बात, पीछेके दो श्लोकों में तथा इस श्लोकके पूर्वार्धमें भी योग (समता) का ही प्रसङ्ग है, कुशलताका प्रसङ्ग ही नहीं है। इसलिये भी कर्मोंमें योग ही कुशलता है, यह अर्थ लेना प्रसङ्गके अनुसार युक्तियुक्त है।",
